$colorText-1: #333;
$colorText-2: #BDBDBD;
$colorText-3: #F2F2F2;
$colorText-4: #F9F9F9;
$colorText-4: #FBFBFB;
$marginEnd: 120px;
$marginBottomTitle: 60px;

@mixin displayCentre {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin underline {
    content: "";
    display: block;
    height: 1px;
    background: $colorText-1;
} 

@mixin burgerMenu {
    position: absolute;
    right: 0;
    display: block;
    height: 2px;
    background: #333;
    border-radius: 2px;
}

@mixin minTitle {
    font-family: 'TT Hoves';
    font-size: 35px;
    margin-bottom: 30px;
    font-weight: 700;
}
