.template {
    & .container {
        justify-content: space-between;
        margin-bottom: $marginEnd;
    }
    &__slider {
        &-title {
            margin: 336px 0 90px;
            color: $colorText-2;
            font-family: 'TT Hoves Light';
            font-size: 64px;
            font-weight: 300;
            line-height: 64px;
            text-transform: uppercase;
            & span {
                color: $colorText-1;
                display: block;
                font-family: TT Hoves;
                font-weight: 700;
            }
        }
        &-arrows {
            display: flex;
            justify-content: flex-start;
            margin-bottom: 90px;
            & .arrow {
                cursor: pointer;

                @include displayCentre;

                width: 53px;
                height: 53px;
                border: 1px solid $colorText-3;

                margin-right: 23px;
                &.next {
                    transform: rotate(180deg);
                }
                &.allow {
                    background: $colorText-4;
                }
            }
        }
        &-counter {
            display: flex;
            justify-content: space-between;
            max-width: 142px;
            width: 100%; 
            margin-bottom: 77px;
            & h2 {
                font-family: 'TT Hoves';
                font-size: 24px;
                font-weight: 400;
                line-height: 24px;
                color: $colorText-2;
            }
            & img {
                display: block;
            }
        }
    }
    &__image {
        position: relative;
        & .button {
            position: absolute;
            left: 0;
            bottom: 0;
        }
    }
}

@media (max-width: 1130px) {
    .template {
        position: relative;
        & .container {
            flex-direction: column;
        }
        &__slider {
            &-title {
                margin: 80px 0 35px;
                font-size: 45px;
                & span {
                    display: inline-block;
                }
            }
            &-arrows {
                margin-bottom: 40px;
            }
            &-counter {
                margin-bottom: 40px;
            }
        }
    }
}

@media (max-width: 860px) {
    .template {
        &__slider-element {
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: absolute;
            bottom: 0;
            width: calc(100% - 40px);
            transform: translate(0, 100px);
        }
    }
}

