.tasks {
    & .container {
        flex-direction: column;
        margin-bottom: $marginEnd;
    }
    &-title {
        margin-bottom: 60px;
    }
    &__container {
        display: flex;
        justify-content: space-between;
        &-info {
            font-family: 'TT Hoves';
            font-size: 22px;
            line-height: 36px;
            display: flex;
            &:nth-child(1)::before {
                content: url("assets/img/number_1.svg");
                margin-right: 30px;
            }
            &:nth-child(2)::before {
                content: url("assets/img/number_2.svg");
                margin-right: 30px;
            }
            &:nth-child(odd) {
                max-width: 415px;
            }
            &:nth-child(even) {
                max-width: 570px;
            } 
        }
    }
}

@media (max-width: 1020px) {
    .tasks {
        & .container {
            margin-bottom: 90px;
        }
        &__container {
            flex-direction: column;
            &-info {
                margin-bottom: 15px;
                max-width: 100%!important;
                &::before {
                    align-self: center;
                }
                &:nth-child(1)::before {
                    margin-right: 60px;
                }
            }
        }
    }
}

@media (max-width: 600px) {
    .tasks {
        & .info-title {
            @include minTitle;
            text-align: center;
        }
        &__container-info {
            flex-direction: column;
            text-align: center;
            font-size: 18px;
            margin-bottom: 30px;
            &:nth-child(odd)::before {
                margin: 0;
            }
            &:nth-child(even)::before {
                margin: 0;
            } 
        }
    }
}