.portfolio {
    & .info-title {
        margin-bottom: $marginBottomTitle;
    }
    & .container {
        flex-direction: column;
        margin-bottom: $marginEnd;
    }
    &__box {
        display: flex;
        justify-content: space-between;
        gap: 30px;
        margin-bottom: 30px;
    }
    &-main {
        box-shadow: inset 10px 600px 5px 0px rgb(51 51 51/ 80%);
        position: relative;
        max-width: 570px;
        & img {
            position: absolute;
            top: 0;
            z-index: -5;
        }
    }
    &__title {
        color: #FFF;
        font-family: 'TT Hoves';
        font-size: 64px;
        font-weight: 700;
        line-height: 64px;
        padding: 48px 79px 14px; 
    }
    &__link {
        color: #FFF;
        font-family: Gothic A1;
        font-size: 12px;
        font-weight: 400;
        line-height: 12px;
        letter-spacing: 2.4px;
        text-transform: uppercase;
        padding: 0 12px 53px 79px;
        &::after {
            content: url('assets/img/arrow-white.svg');
            display: inline-block;
            transform: rotate(180deg);
            margin-left: 12px;
        }
    }
    &-button {
        background: $colorText-1;
        align-self: flex-end;
        & p {
            color: #FFF;
            &::after {
                content: url('assets/img/arrow-white.svg')!important;
            }
        }
    }
}

@media (max-width: 1000px) {
    .portfolio {
        &__box:nth-child(2) {
            flex-direction: column;        
        }
        &-main {
            max-width: 100%;
        }
        &__title {
            padding: 110px 79px 14px;
        }  
        &__link {
            padding: 0 12px 110px 79px;
        }
        &-button {
            align-self: flex-start;
        }
    }
}

@media (max-width: 600px) {
    .portfolio {
        & .info-title {
            @include minTitle;
        }
        &__box {
            gap: 10px;
            margin-bottom: 10px;
        }
        &__title {
            padding: 50px 20px 14px;
            font-size: 40px;
            line-height: 40px;
            font-size: 20px;
        }
        &__link {
            padding: 0 12px 50px 22px;
        }
        & .button p {
            padding: 20px 0;
        }
    }
}