.about {
    max-width: 1170px;
    height: 435px;
    margin: 0 auto 150px;
    background: $colorText-4;
    &__image {
        margin: 30px 30px 0 100px;
        display: flex;
        &-item {
            display: flex;
            flex-direction: column;
            margin: 0 30px 0 0;
            & .image-3 {
                margin-bottom: 30px;
            } 
        }
        & .image-1 {
            margin-top: 30px;
            width: 270px;
            height: 345px;           
        }
    }
    &__info {
        margin: 30px 0;
        &-title {
            margin-bottom: 20px;
        }
        &-text {
            color: #000;
            font-family: 'TT Hoves Light';
            font-size: 16px;
            font-weight: 300;
            line-height: 25px;
            margin: 0 30px 20px 0;
        }
    }
}

@media (max-width: 1130px) {
    .about{
        height: auto;
        &__image {
            margin: 30px 30px 20px 0px;
            & .image-1 {
                margin-top: 0;
                height: auto;
            }
        }
        &__info {
            &-title {
                @include minTitle;
            }
            &-text {
                margin: 0 0 20px 0;
            }
        }
    }
}

@media (max-width: 930px) {
    .about {
        & .container {
            flex-direction: column-reverse;
            overflow: hidden;
        }
        &__image {
            margin: 30px 0px 20px 0;
            align-self: center;
            flex-direction: row-reverse;
            &-item {
                margin: 0 0 0 30px;
            }
            
        }
    }
}