.header {
    padding: 30px 0;
    &__logo {
        margin-right: calc(330px - 30px);
    }
    &__nav {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        &-item {
            margin: 0 30px;
            & a {
                position: relative;
                padding: 5px 0;

                font-family: 'TT Hoves Light';
                font-size: 12px;
                font-weight: 400;
                line-height: 12px;
                letter-spacing: 2.4px;
                text-transform: uppercase;
                color: $colorText-1;

                &::after, &::before {
                    @include underline;
                    left: 50%;
                    position: absolute;
                    width: 0;
                    transition: all 0.3s ease;
                }
                &::after {
                    bottom: 0;
                }
                &::before {
                    top: 0;
                }
                &:hover::after, &:hover::before { 
                    width: 100%; 
                    left: 0; 
                  }
                &.active {
                    &::after, &::before {
                        @include underline;
                        width: 100%;
                        position: static;
                    }
                    &::after {
                        margin-top: 3px;
                    }
                    &::before {
                        margin-bottom: 3px;
                    }
                }
            }
        }
    }
    &__icon {
        display: none;
        position: relative;
        width: 50px;
        height: 50px;
        border-radius: 4px;
        background: rgb(255, 255, 255);
        box-shadow: 0px 0px 22px -12px rgba(0,0,0,0.75);
        & span {
            @include burgerMenu;
            top: 50%;
            width: 34%;
        }
        &::after {
            content: "";
            @include burgerMenu;
            top: 20%;
            width: 54%;
        }
        &::before {
            content: "";
            @include burgerMenu;
            bottom: 20%;
            width: 75%;
        }
    }
}

@media (max-width: 1130px) {
    .header {
        & .container {
          justify-content: space-between;  
        }
        &__logo {
            margin-right: 0;
        }
    }
}

@media (max-width: 860px) {
    .header {
        
        &__icon {
            display: block;
        }
        &__panel { 
            display: none;
            position: absolute;
            right: 50%;
            top: 100%;
            transform: translate(50%,0);
            width: 100%;
            background: #f3f3f3;
            margin: 10px 0;
            z-index: 10;
        }
        &__nav {
            flex-direction: column;
            &-item {
                margin: 20px 30px;
            }
        }
    }
}