.contact {
    margin-bottom: 120px;
    .container {
        display: block;
    }
    &__container {
        display: flex;
        margin-bottom: 22px;
    }
    &__inputs {
        margin-right: 25px;
        display: flex;
        flex-direction: column;
        max-width: 391px;
        width: 100%;
        &-item {
            margin-bottom: 22px;
            border: none;
            background: #F3F3F3;
            padding: 18px 22px;
            font-family: 'TT Hoves Light';
            color: #8D8D8D;
        }
        & .textarea_item {
            height: 147px;
            resize: none;
            margin-bottom: 0;
        }
    }
    &__check {
        display: flex;
        cursor: pointer;
        margin-bottom: 22px;
        & p {
            max-width: 331px;
            width: 100%;
        }
        & .custom-checkbox {
            display: inline-block;
            width: 24px;
            height: 24px;
            border-radius: 4px;
            background: #F3F3F3;
            vertical-align: sub;
            position: relative;
            margin-right: 11px;
            &::before {
                content: "";
                display: inline-block;
                background-image: url("assets/img/check.svg");
                background-size: contain;
                background-repeat: no-repeat;
                width: 12px;
                height: 12px;
                position: absolute;
                left: 50%;
                right: 50%;
                transform: translate(-50%, 50%) scale(0);
                transition: 0.2s ease-in;
            }
        }
        & .real-checkbox {
            width: 0;
            height: 0;
            opacity: 0;
            position: absolute;
            z-index: -1;
            &:checked + .custom-checkbox::before {
                transform: translate(-50%, 50%) scale(1);
            }
        }

    }

    & .button {
        background-color: #333;
        & p{
            color: #FFF;
            &::after {
                content: url(assets/img/arrow-white.svg)!important;
                transform: rotate(180deg);
            }
        }
    }

}