body {
    font-family: 'TT Hoves';
}

.container {
    position: relative;
    display: flex;

    max-width: 1210px;
    width: 100%;
    margin: 0 auto;
    padding: 0 20px;

}

.image-fluid {
    width: 100%;
    height: 100%;
}

.info-title {
    font-family: 'TT Hoves Light';
    font-size: 64px;
    font-weight: 300;
    line-height: 64px;
    color: $colorText-2;
    margin-bottom: 35px;
}

.button {
    cursor: pointer;
    @include displayCentre;
    max-width: 222px;
    width: 100%;
    background: white;
    & p {
        font-family: TT Hoves;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 12px;
        letter-spacing: 2.4px;
        text-transform: uppercase;
        padding: 30px 0;
        &::after {
            content: url('assets/img/arrow.svg');
            transform: rotate(180deg);
            margin-left: 12px;
            display: inline-block;
        }
    }
}
